"use client";
import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogFooter,
  DialogTitle,
} from "@/components/ui/dialog"; // Импортируем компоненты Dialog и DialogTitle
import { Button } from "@/components/ui/button";
import { VisuallyHidden } from "@radix-ui/react-visually-hidden"; // Импортируем компонент для скрытия контента от визуального отображения
import { usersData } from "@/lib/utils";

export default function DataTable() {
  const [searchTerm, setSearchTerm] = useState("");
  const [modalOpen, setModalOpen] = useState(false); // Состояние для открытия модалки
  const [editedUser, setEditedUser] = useState({
    id: "",
    first_name: "",
    last_name: "",
    phone_number: "",
    role: "",
    third_name: "",
    inn: "",
    email: "",
    sponsor_id: "",
    passport: "",
    passport_issued: "",
    passport_whom_by: "",
    country: "",
    degree_id: "",
    register_id: "",
    is_active: false,
    left_pv: 0,
    right_pv: 0,
    mb: 0,
    balance: 0,
  });

  const filteredData = usersData.filter(
    (item) =>
      item.first_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.last_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.email.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.role.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const handleEditClick = (user) => {
    setEditedUser({ ...user }); // Заполняем данные для редактирования
    setModalOpen(true); // Открываем модальное окно
  };

  const handleSaveChanges = () => {
    // Логика для сохранения изменений
    console.log("Updated user:", editedUser);
    setModalOpen(false); // Закрываем модальное окно после сохранения
  };

  return (
    <div className="p-4">
      <Input
        type="text"
        className="mb-4"
        placeholder="Search..."
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
      />
      <div className="overflow-x-auto">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>ID</TableHead>
              <TableHead>Name</TableHead>
              <TableHead>Role</TableHead>
              <TableHead>Status</TableHead>
              <TableHead>Balance</TableHead>
              <TableHead>Actions</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {filteredData.map((row) => (
              <TableRow key={row.id}>
                <TableCell>{row.id}</TableCell>
                <TableCell>{`${row.first_name} ${row.last_name}`}</TableCell>
                <TableCell>{row.role}</TableCell>
                <TableCell>
                  <span
                    className={`${
                      row.is_active
                        ? "text-green-600 font-semibold"
                        : "text-red-600 font-semibold"
                    }`}
                  >
                    {row.is_active ? "Active" : "Inactive"}
                  </span>
                </TableCell>
                <TableCell>{row.balance}</TableCell>
                <TableCell className="flex space-x-2">
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => handleEditClick(row)}
                  >
                    Edit
                  </Button>
                  <Button variant="destructive" size="sm">
                    Delete
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>

      {/* Модальное окно для редактирования пользователя */}
      {modalOpen && (
        <Dialog open={modalOpen} onOpenChange={setModalOpen}>
          <DialogContent className="max-w-5xl w-full p-8">
            {/* DialogTitle added for screen readers */}
            <DialogTitle>
              <VisuallyHidden>Edit User</VisuallyHidden>
            </DialogTitle>
            <DialogHeader>
              <h3 className="text-xl font-semibold mb-6">Edit User</h3>
            </DialogHeader>
            <div className="grid grid-cols-2 gap-6">
              <div>
                <div className="mb-4">
                  <Label htmlFor="first_name">First Name</Label>
                  <Input
                    id="first_name"
                    value={editedUser.first_name}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        first_name: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="phone_number">Phone Number</Label>
                  <Input
                    id="phone_number"
                    value={editedUser.phone_number}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        phone_number: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="role">Role</Label>
                  <Input
                    id="role"
                    value={editedUser.role}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, role: e.target.value })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="inn">INN</Label>
                  <Input
                    id="inn"
                    value={editedUser.inn}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, inn: e.target.value })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="passport">Passport</Label>
                  <Input
                    id="passport"
                    value={editedUser.passport}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, passport: e.target.value })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="country">Country</Label>
                  <Input
                    id="country"
                    value={editedUser.country}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, country: e.target.value })
                    }
                  />
                </div>
              </div>
              <div>
                <div className="mb-4">
                  <Label htmlFor="last_name">Last Name</Label>
                  <Input
                    id="last_name"
                    value={editedUser.last_name}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        last_name: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="email">Email</Label>
                  <Input
                    id="email"
                    value={editedUser.email}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, email: e.target.value })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="passport_issued">Passport Issued</Label>
                  <Input
                    id="passport_issued"
                    value={editedUser.passport_issued}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        passport_issued: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="passport_whom_by">Issued By</Label>
                  <Input
                    id="passport_whom_by"
                    value={editedUser.passport_whom_by}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        passport_whom_by: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="register_id">Register ID</Label>
                  <Input
                    id="register_id"
                    value={editedUser.register_id}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        register_id: e.target.value,
                      })
                    }
                  />
                </div>
                <div className="mb-4">
                  <Label htmlFor="balance">Balance</Label>
                  <Input
                    id="balance"
                    value={editedUser.balance}
                    onChange={(e) =>
                      setEditedUser({ ...editedUser, balance: e.target.value })
                    }
                  />
                </div>
                <div className="flex items-center">
                  <Label htmlFor="is_active" className="mr-2">
                    Active Status
                  </Label>
                  <input
                    type="checkbox"
                    id="is_active"
                    checked={editedUser.is_active}
                    onChange={(e) =>
                      setEditedUser({
                        ...editedUser,
                        is_active: e.target.checked,
                      })
                    }
                  />
                </div>
              </div>
            </div>
            <DialogFooter className="mt-6">
              <Button
                variant="outline"
                onClick={() => setModalOpen(false)} // Закрываем модальное окно
              >
                Cancel
              </Button>
              <Button variant="solid" onClick={handleSaveChanges}>
                Save Changes
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      )}
    </div>
  );
}
